.hero {
  height: 300px;
  background-size: cover;
  background-position: center;
  position: relative;
  background-color: navy;
  border-bottom: 5px red solid;
}

.hero::after {
  content: "";
  display: block;
  opacity: .4;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: black;
}

.hero * {
  position: relative;
  z-index: 2;
}

.hero h1, .hero h2 {
  color: white;
}

.hero h1 {
  font-size: 40px;
  padding-top: 100px;
  margin-top: 0;
}

.hero h2 {
  font-size: 20px;
  padding-top: 20px;
}
